.container-slider {
    /* max-width: 200px;
    height: 400px; */
    width: 30vw;
    height: 30vh;
    margin-left: 5vw;
    /* margin: 100px auto 0; */
    padding: 20px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    display: flex;
    border-radius: 20px;
    background-color: white;

  }
  @media screen and (max-width: 700px){
    .container-slider {
      margin: 100px 10px 0;
    }
  }
  .slide {
    width: 100%;
    height: 100%;
    /* position: absolute; */
    color: orange;
    transition: color ease-in-out 0.4s;

    /* Content Allignment */
    text-align: center;
  }
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .active-anim {
    color: rgba(0, 0, 0, 1);
  }
  
  .btn-slide {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .prev {
    top: 50%;
    left: -40%;
    transform: translateY(-60%);
  }
  .next {
    top: 50%;
    right: -40%;
    transform: translateY(-60%);
  }
  
  
  .container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }
  .dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f1;
  }
  .dot.active {
    background: rgb(32, 32, 32);
  }

  .slide-content {
    display: flex;
    justify-content: left;
    align-content: left;
    text-align: left;
    flex-direction: column;
    white-space:pre-wrap;
  }

  .slide-content-header {
    justify-content: top;
  }

  .slider-button {
    position: relative;
    top: 50%;
    position: relative;
    font-size: 30px;
    transition-duration:0.1s;
    color: grey;
  }

  .slider-button:hover {
    display: flex;
    position: relative;
    font-size: 35px;
    /* border:2px solid;
    border-radius: 10px; */
    color: black;
  }